"use client";

import { lazorkit } from "../lib/lazorkit";
import { useState } from "react";

type Props = {
  onSuccess: () => void;
};

export default function PayWithSolana({ onSuccess }: Props) {
  const [isPaying, setIsPaying] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handlePayment = async () => {
    setIsPaying(true);
    setError(null);

    try {
      // Lazorkit Paymaster gasless transaction
      const tx = await lazorkit.send({
        to: "DemoMerchantWalletAddressHere", // Demo address for testing
        amount: 5,
        token: "USDC",
      });

      console.log("Transaction hash:", tx.hash);

      // Call parent callback
      onSuccess();
    } catch (err: any) {
      console.error(err);
      setError(err.message || "Payment failed");
    } finally {
      setIsPaying(false);
    }
  };

  return (
    <div style={{ marginTop: "1.5rem" }}>
      <h3>Demo Payment</h3>
      <p>Merchant: Demo Merchant</p>
      <p>Amount: 5 USDC</p>

      <button onClick={handlePayment} disabled={isPaying}>
        {isPaying ? "Processing..." : "Pay with Solana (Gasless)"}
      </button>

      {error && <p style={{ color: "red" }}>Error: {error}</p>}
    </div>
  );
}

